.button-form {
  margin: 0 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    margin: 0 70px 60px;
  }

  @media (min-width: @desktop-width) {
    width: 1224px;
    margin: 0 auto 78px;
  }
}

.button-form__text {
  margin: 0 20px 32px;
  padding: 0;
  font-family: @lato;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 16px;
    margin: 17px 0 0 57px;
  }

  @media (min-width: @desktop-width) {
    margin: 20px 0 0 79px;
  }
}
